html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    background-color: #f6f9fc;
    color: $color-font;
}

img {
    max-width: 100%;
}

h1 {
    font-size: 39px;
    font-family: 'Akrobat', sans-serif;
    line-height: 1.5;
    margin: 20px 0;
    font-weight: 800;
    color: $color-heading;

    span {
        color: $color-blue;
    }
}

@media only screen and (max-width : 1200px) {
    h1 {
        font-size: 29px;

    }

}

@media only screen and (max-width : 540px) {
    h1 {
        font-size: 22px;

    }

}

h2 {
    font-size: 39px;
    font-family: 'Akrobat', sans-serif;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 20px;
    text-transform: uppercase;
    color: $color-heading;

    span {
        color: $color-blue;
    }
}

@media only screen and (max-width : 1200px) {
    h2 {
        font-size: 27px;

    }

}

@media only screen and (max-width : 540px) {
    h2 {
        font-size: 20px;

    }

}


p {
    font-size: 16px;
    line-height: 1.5;
    color: $color-font;
}

@media only screen and (max-width : 540px) {
    p {
        font-size: 14px;
    }
}

a {
    text-decoration: none;
    transition: all .3s;
}




@media only screen and (max-width : 992px) {
    .container-lg {
        padding-right: 30px;
    }
}
